html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: "DM Sans", sans-serif;
  font-size: 1.8rem;
  background-color: hsl(27deg, 66%, 92%);
  height: 100vh;
  position: relative;
}
@media (min-width: 1440px) {
  body {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.container {
  width: min(95%, 120rem);
  margin-inline: auto;
}
@media (min-width: 1440px) {
  .container {
    width: min(30%, 120rem);
  }
}

.section {
  margin-top: 2rem;
}

hr {
  border: 1px solid hsl(27deg, 66%, 92%);
  margin-block: 2rem;
}

.my-balance {
  margin-top: 8rem;
  background-color: hsl(10deg, 79%, 65%);
  border-radius: 1rem;
  padding: 2rem;
  color: hsl(33deg, 100%, 98%);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1440px) {
  .my-balance {
    margin-top: 0;
  }
}
.my-balance__title {
  margin: 0;
}
.my-balance__amount {
  margin-top: 0.8rem;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 3rem;
}

.balance {
  background-color: hsl(33deg, 100%, 98%);
  border-radius: 1rem;
  padding: 2rem;
}
.balance__heading {
  margin-top: 1rem;
  margin-bottom: 5rem;
}
.balance__graphics {
  display: flex;
  justify-content: space-around;
  gap: 1.2rem;
  height: 15rem;
}
.balance__graphic {
  --color: v.$SoftRed;
  border-radius: 0.3rem;
  background-color: hsl(10deg, 79%, 65%);
  height: 10%;
  position: relative;
}
.balance__graphic:hover, .balance__graphic:active {
  cursor: pointer;
  background-color: #ef8a76;
}
.balance__graphic::before, .balance__graphic::after {
  --scale: 0;
  --tooltip-color: #333;
  --arrow-size: 1rem;
  position: absolute;
  top: -0.25rem;
  left: 50%;
  transform: translateX(-50%) translateY(var(--translate-y, 0)) scale(var(--scale));
  transition: 50ms transform;
  transform-origin: bottom center;
}
.balance__graphic::before {
  --translate-y: calc(-100% - var(--arrow-size));
  content: attr(data-title);
  font-size: 1.2rem;
  color: #fff;
  padding: 0.5rem;
  border-radius: 0.3rem;
  text-emphasis: center;
  width: max-content;
  max-width: 100%;
  background-color: var(--tooltip-color);
}
.balance__graphic:hover::before, .balance__graphic:hover::after {
  --scale: 1;
}
.balance__graphic::after {
  --translate-y: calc(-1 * var(--arrow-size));
  content: "";
  border: var(--arrow-size) solid transparent;
  border-top-color: var(--tooltip-color);
  transform-origin: top center;
}
.balance__graphic-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 4rem;
}
@media (min-width: 1440px) {
  .balance__graphic-container {
    width: 5rem;
  }
}
.balance__graphic-text {
  text-align: center;
  margin: 1rem 0 0 0;
  color: hsl(28deg, 10%, 53%);
  font-size: 1.5rem;
}

#balance-2 {
  height: 50%;
}

#balance-3 {
  height: 100%;
  background-color: hsl(186deg, 34%, 60%);
}

.summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.summary__total {
  color: hsl(28deg, 10%, 53%);
  font-size: 1.5rem;
  margin-bottom: 0.3rem;
}
.summary__save {
  color: hsl(25deg, 47%, 15%);
  margin-block: 0;
  font-size: 3rem;
  font-weight: 700;
}
.summary__percentage {
  margin-bottom: 0;
  color: hsl(28deg, 10%, 53%);
}
.summary__percentage span {
  color: hsl(25deg, 47%, 15%);
  text-align: right;
  display: block;
  font-weight: 700;
}

.attribution {
  position: absolute;
  text-align: center;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}